.tabs {
  &__nav {
    @include flex-all-center;
    margin-bottom: 42px;

    @include tablet {
      margin-bottom: 24px;
    }
    @include mobile {
      flex-wrap: wrap;
      justify-content: start;
      gap: 1em;
    }
  }

  &__nav-item:not(:last-child) {
    margin-right: 107px;

    @include mobile {
      margin-right: 1.75em;
    }

  }

  &__nav-btn {
    font-weight: 400;
    font-size: 28px;
    line-height: 143%;
    text-align: center;
    color: rgba(255, 255, 255, 0.5);
    transition: color .3s ease-in-out;

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 20px;
    }

    &--active {
      color: $light-color;
    }
  }

  &__panel {
    display: none;

    &--active {
      display: block;
    }

    p {
      margin-top: 0;
      margin-bottom: 0.5em;
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.01em;
      color: $light-color;

      @include mobile {
        font-size: 14px;
      }
    }
  }


}

