.workshops {
  padding-top: 80px;

  &__title {
    margin: 0;
    margin-bottom: 0.875em;
  }

  &__descr {
    margin: 0;
    margin-bottom: $section-offset;
  }

  &__item {
    grid-column: 3 span;

    @include small-desktop {
      grid-column: 4 span;
    }
    @include for-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 12 span;
    }
  }

  &__item .workshops-card {
    height: 100%;
  }
}

.workshops-card {
  display: flex;
  flex-direction: column;

  &__image {
    --br-img-card: 10px;
    @include flex-all-center;
    overflow: hidden;
    margin-bottom: 20px;
    border-radius: var(--br-img-card);
    width: 100%;
    max-height: 280px;

    img {
      border-radius: var(--br-img-card);
      max-width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: $light-color;
  }

  &__descr {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: $light-color;
  }

  &__price {
    margin: 0;
    margin-top: auto;
    margin-bottom: 15px;
    font-weight: 800;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: $light-color;
  }

  &__btns {
    @include flex-all-sb;
  }

  &__btn {
    padding: 16px;
    width: 100%;
    font-size: 16px;
    text-align: center;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
