.prodact-card {
  padding-bottom: $section-offset;
  background-color: $bg-dark-color;

  &__content {
    position: relative;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__info {
    grid-column: 7 / 6 span;

    @include small-desktop {
      grid-column: 12 span;
    }
  }
  &__top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: $section-offset;
    font-size: 24px;
    line-height: 92%;
    color: $light-color;

    @include small-desktop {
      position: absolute;
      top: 0;
      right: 0;
    }
    @include tablet {
      font-size: 20px;
    }
    @include small-tablet {
      position: relative;
      flex-direction: row;
      align-items: center;
      gap: 1em;
      margin-bottom: 1.25em;
    }
  }

  &__price {
    display: inline-block;
    padding: 24px 80px 16px;
    border-radius: 100px 100px 0 0;
    font-weight: 600;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    background-color: #404040;

    @include tablet {
      padding: 1em 3em 0.8em;
    }

    @include small-tablet {
      display: block;
      flex: 1 1 50%;
      padding: 1em;
      border-radius: 300px;
      text-align: center;
    }
  }

  &__btn {
    display: inline-block;
    padding: 30px 111px;
    border-radius: 0 150px 150px 300px;
    font-weight: 800;
    font-size: inherit;
    line-height: inherit;
    color: inherit;

    @include tablet {
      padding: 1em 3.8em;
    }

    @include small-tablet {
      display: block;
      flex: 1 1 50%;
      padding: 1em;
      border-radius: 300px;
      text-align: center;
    }
  }
}
.prodact-card-images {
  grid-column: 6 span;
  overflow: hidden;

  @include small-desktop {
    grid-column: 12 span;
    padding-right: 380px;
  }
  @include tablet {
    padding-right: 280px;
  }
  @include small-tablet {
    padding-right: 0;
  }
}
.prodact-card-images-slider {
  // min-height: 465px;
  margin-bottom: 20px;
}
.card-img {
  --br-img-card: 10px;
  // @include flex-v-center;
  // padding: 20px;
  border-radius: var(--br-img-card);
  width: 100%;
  height: 100%;
  cursor: pointer;

  img {
    display: block;
    border-radius: var(--br-img-card);
    height: auto;
    max-width: 100%;
    object-fit: cover;
  }
}

.prodact-card-images-nav {
  overflow: hidden;
  // max-height: 96px;

  .swiper-slide {
    border-radius: 5px;
    min-width: 90px;
    opacity: 0.6;
    transition: opacity 0.3s ease-in-out;
    cursor: pointer;

    .swiper-slide-thumb-active {
      opacity: 1;
    }
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }
}

.options {
  @include flex-h-center;
  flex-direction: column;
  gap: 1.5em;


  &__item {
    display: grid;
    grid-template-columns: 0.8fr 2fr;
    column-gap: 10px;
    span {
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.01em;
      color: $light-color;
    }
  }
}


