.promo {
  color: $light-color;
  background-color: $bg-dark-color;
  background-image: url(../img/promo/promo-bg.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top right;
  overflow: hidden;

  &__container {
    position: relative;
    @include flex-v-center;
    justify-content: space-between;
    padding: 136px 15px 0;
    min-height: 650px;

    @include tablet{
      min-height: 550px;
    }
    @include small-tablet {
      padding-top: 116px;
    }
  }

  &__content {
    position: relative;
    z-index: 3;
    max-width: 620px;

    @include tablet {
      max-width: 520px;
    }
    @include mobile {
      text-align: center;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 18px;
    font-style: italic;
    font-weight: 600;
    font-size: 90px;
    line-height: 104%;
    color: $light-color;
    text-transform: uppercase;

    @include tablet {
      font-size: 62px;
    }

    @include small-tablet {
      font-size: 48px;
    }

    &--ofset{
      padding-left: $section-offset;
      font-style: normal;
      font-weight: 800;
      font-size: inherit;
      line-height: 104%;
      text-transform: none;

      @include small-tablet {
        padding-left: 25px;
      }
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: $gutter;
    max-width: 616px;
    font-weight: 500;
    font-size: 18px;
    line-height: 160%;
    color: $light-color;

    @include tablet {
      font-size: 16px;
    }
    @include small-tablet {
      max-width: 420px;
      font-size: 14px;
    }
    @include mobile {
      max-width: none;
    }
  }

  &__btn {
    display: inline-block;
    padding: 26px 32px;
    font-size: 20px;

    @include tablet {
      padding: 20px 24px;
    }
  }

  &__list {
    align-items: center;
    gap: 30px;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-weight: 500;
    font-size: $main-size;
    line-height: 145%;
    color: $light-color;
    transform: scale(1);
    transition: color .3s ease-in-out, transform .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: $active-color;
    }
  }

  &__icon {
    fill: currentColor;
  }

  &__image {
    position: absolute;
    right: 0;
    bottom: -20%;
    overflow: hidden;

    @include tablet{
      bottom: -35%;
    }
    @include small-tablet{
      bottom: -30%;
    }

  }
  &__img {
    display: block;
    object-fit: cover;
    width: 600px;
    height: 800px;
    transform: translateX(5%);

    @include tablet{
      transform: scale(0.75) translateX(25%);
    }
    @include small-tablet {
      transform: scale(0.75) translateX(35%);
    }
    @include mobile {
      display: none;
    }
  }
}
.promo__link.focus-visible  {
  transform: scale(1.15);
  color: $accent-color;
}

.promo-altai {
  position: relative;
  background-color: $bg-dark-color;
  height: 100vh;
  max-height: 900px;
  overflow: hidden;

  @include small-tablet {
    max-height: 500px;
  }

  &--bg {
    background-image: url(../img/altai/bg-altai.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
  &__container {
    @include flex-all-center;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  &__title {
    margin: 0;
    padding-top: 40px;
    padding-bottom: 25px;
    width: 100%;
    font-style: italic;
    font-size: 96px;
    line-height: 145%;
    text-transform: capitalize;
    color: $light-color;

    @include tablet {
      font-size: 72px;
    }
    @include small-tablet {
      font-size: 56px;
    }
    @include mobile {
      font-size: 40px;
    }
    @include small-mobile {
    }

    span {
      display: block;
      font-weight: 400;
      font-size: 1em;
      text-align: right;

      @include small-mobile {
        text-align: center;
      }

      &:first-child {
        font-weight: 700;
        font-size: 1.33em;
        text-align: left;

        @include small-mobile {
          text-align: center;
        }

      }
    }
  }
}
