.about {
  padding-top: 80px;

  @include tablet {
    padding: 50px 0 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 0.875em;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
  }
  &__left {
    grid-column: 1 / 8 span;

    @include small-desktop {
      grid-column: 12 span;
    }
  }

  &__sliderblock {
    @include small-desktop {
      margin-bottom: 35px;
    }
  }

  &__video {
    border-radius: 20px;
    height: auto;
    // aspect-ratio: 16 / 9;
    background-color: rgb(42, 43, 47, 0.5);
  }

  &__preview-video {
    --br-img-card: 10px;
    @include flex-all-center;
    border-radius: var(--br-img-card);
    width: 100%;
    height: 100%;
    cursor: pointer;

    img {
      border-radius: var(--br-img-card);
      max-width: 100%;
      object-fit: cover;
    }
  }

  &__descr {
    grid-column: 8 span;
    margin-bottom: $gutter;
    width: 95%;

    p {
      margin: 0;
    }
    p:not(:last-child) {
      margin-bottom: 10px;
    }

    p:first-child,
    p:last-child {
      margin-bottom: 20px;
      font-weight: 700;
    }
  }

  &__list {
    position: relative;
    grid-column: 4 span;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    gap: 25px;
    padding: 30px 26px;
    border-radius: 20px;
    background: radial-gradient(circle at 100% 100%, #15161b 0, #15161b 19px, transparent 19px) 0% 0%/20px 20px no-repeat,
    radial-gradient(circle at 0 100%, #15161b 0, #15161b 19px, transparent 19px) 100% 0%/20px 20px no-repeat,
    radial-gradient(circle at 100% 0, #15161b 0, #15161b 19px, transparent 19px) 0% 100%/20px 20px no-repeat,
    radial-gradient(circle at 0 0, #15161b 0, #15161b 19px, transparent 19px) 100% 100%/20px 20px no-repeat,
    linear-gradient(#15161b, #15161b) 50% 50%/calc(100% - 2px) calc(100% - 40px) no-repeat,
    linear-gradient(#15161b, #15161b) 50% 50%/calc(100% - 40px) calc(100% - 2px) no-repeat,
    linear-gradient(135deg, #2dbbda 0%, #d9dc31 100%);

    @include small-desktop {
      grid-column: 12 span;
    }


  }

  &__item {
    grid-column: 4 span;
    grid-row: 1 span;
    max-width: 500px;

    @include small-desktop {
      grid-column: 2 span;
    }
    @include small-tablet {
      grid-column: 4 span;
    }
  }
}
.about-video-slider {
  margin-bottom: 20px;
}
.about-video-slider-nav {
  overflow: hidden;

  .swiper-slide {
    border-radius: 5px;
    opacity: 0.6;
    transition: opacity 0.3s ease-in-out;
    cursor: pointer;

    .swiper-slide-thumb-active {
      opacity: 1;
    }
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }
}

.about-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;

  &__img {
    margin-bottom: 18px;
  }
  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 20px;
    line-height: 34px;
    color: $light-color;
  }
  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: $main-size;
    line-height: 28px;
    text-align: center;
    color: $light-color;
  }
}

